import SVG from "../images/logo_jc.svg"; 

import FullWidthSection from '../layout/FullWidthSection';
import MaxWidthSection from '../layout/MaxWidthSection';
import Row from '../layout/Row';

const Content = styled(MaxWidthSection) `
padding-top:8%;
// background-color: #B83B00;
margin-top: 10%;
`;


var moment = require('moment-timezone');
var time = moment().tz("America/Toronto").format('hh:mm A');

