import  React from 'react';
import styled from 'styled-components'
import { Link } from "react-router-dom";
import point from "../images/symbol.svg"; 
import {ReactComponent as Point} from '../images/symbol.svg';
import  { ScrollRotate } from 'react-scroll-rotate';

//import FullWidthSection from '../layout/FullWidthSection';
import Row from '../layout/Row';
import MaxWidthSection from '../layout/MaxWidthSection';

const Header = styled(MaxWidthSection) `
  
`;

const Nav = styled(Row)`
  
`;
 
const Topnav = () => {
    return(
        <Header as="header">
          <Nav as="nav" sd={1} ed={12} sm={1} em={6} ss={1} se={6}>
            <div class="name">
              {/* <a href="#"><Link to='/' onClick={() =>  document.body.style.backgroundColor = '#ffffff'}>Works</Link></a> */}
              {/* <a href="#"><Link to='/info' onClick={() =>  document.body.style.backgroundColor = '#B83B00'}>Info</Link></a> */}
              {/* <a href="#"><Link to='/'>Resources</Link></a> */}
              <a class="name" href="#"><Link to='/' onClick={() =>  document.body.style.backgroundColor = '#ffffff'}>JuanCastro</Link></a>\
              <ScrollRotate><Point/></ScrollRotate>
              <a class="name" href="#">Designer</a>
            </div>
            
            {/* <div class="center">
               <a href="#"><Link to='/' onClick={() =>  document.body.style.backgroundColor = '#ffffff'}>Juan Castro</Link></a>
            </div>

            <div class="right">
              <a href="#"><Link to='/info' onClick={() =>  document.body.style.backgroundColor = '#B83B00'}>Info</Link></a>
              <a href="#">fun ☻</a>
            </div>   */}        
          </Nav>

          <Nav as="nav" class="topnav" sd={1} ed={2} sm={1} em={6} ss={1} se={6}>
          
            <a href="#">Interactive designer</a>
           
          </Nav>
          <Nav as="nav" class="topnav" sd={5} ed={1} sm={1} em={6} ss={1} se={6}>
            
            
            <a href="#">SW ● 23</a>
           
                      
          </Nav>
          <Nav as="nav" class="topnav" sd={12} ed={1} sm={1} em={6} ss={1} se={6}>
            
            <div class="right">
              <a href="#"><Link to='/info' onClick={() =>  document.body.style.backgroundColor = '#B83B00'}>Info, </Link></a>
              <a href="#">fun ☻</a>
            </div>          
          </Nav>
        </Header>
    );

}

export default Topnav;